* {
  font-family: 'Nunito', 'Roboto Mono', monospace;
}

html {
  /* background-color: #131313; */
	background-image: url("https://i.imgur.com/XuFxkfa.jpg");
	background-size: contain;
	background-position: center;
  background-repeat: no-repeat;
  background-attachment: fixed;
	background-color: #000;
}

:root {
  --color-p: #b39ddb;
  --color-g: #89c4a7;
  --color-y: #d6c17a;
}

li {
  list-style-type: none;
}

/* ===== Scrollbar CSS ===== */
/* Firefox */
* {
  scrollbar-width: auto;
  scrollbar-color: #8f54a0 #000000;
}

/* Chrome, Edge, and Safari */
*::-webkit-scrollbar {
  width: 13px;
  position: fixed; /* Set position to fixed */
  z-index: 9999; /* Set a high z-index value to ensure it appears on top */
}

*::-webkit-scrollbar-track {
  background: #000000;
}

*::-webkit-scrollbar-thumb {
  background-color: #8f54a0;
  border-radius: 13px;
  border: 3px solid #000000;
}
#logo text{
  -webkit-user-select: none;
          user-select: none;
  font-family:'Roboto Mono';
  font-size:14px;
}

#logo tspan {
  font-family:'Nunito', monospace;
  font-size: 14px;
}

#logo .star {
  fill:var(--color-y);
}

text .tspan-0 {
  fill:var(--color-p);
}

text .tspan-1 {
  fill:var(--color-g);
}


#logo .broken{
  stroke-dasharray:8;
}

#logo .filled{
  fill:black;
}


#logo .end_marked_arrow{
  marker-end:url(#arrow);
}

#logo .start_marked_arrow{
  marker-start:url(#arrow);
}

#logo .end_marked_diamond{
  marker-end:url(#diamond);
}

#logo .start_marked_diamond{
  marker-start:url(#diamond);
}

#logo .end_marked_circle{
  marker-end:url(#circle);
}

#logo .start_marked_circle{
  marker-start:url(#circle);
}

#logo .end_marked_open_circle{
  marker-end:url(#open_circle);
}

#logo .start_marked_open_circle{
  marker-start:url(#open_circle);
}

#logo .end_marked_big_open_circle{
  marker-end:url(#big_open_circle);
}

#logo .start_marked_big_open_circle{
  marker-start:url(#big_open_circle);
}

.fade {
  opacity: 1;
  transition: opacity 0.5s ease-in-out;
}


.animate-spin-slow {
  animation: spin 10s linear infinite;
  transition: transform 0.3s ease-in-out;
}

#svgCont {
  height: 100%;
  width: 100%;
}

#svgCont:hover .animate-spin-slow {
  animation: spin-reverse 5s linear infinite;
  animation-fill-mode: both;
  transform: rotate(180deg);
  cursor: pointer;
}

@keyframes spin {
  100% {
    transform: rotate(360deg);
  }
}

@keyframes spin-reverse {
  100% {
    transform: rotate(-360deg);
  }
}

.hero .title, 
.hero .nickname,
.hero .subtitle {
  color: #d6c17a;
  text-align: center;
  margin: 0;
  padding: 0;
}
ul {
  padding-inline-start: 0;
}

